"use client"
import { useSelector } from "react-redux";
import Image from "next/image";
export default function PaymentResumePage() {
  const cart = useSelector((state) => state.cart.items); // Acceso al estado global del carrito
  const total = useSelector((state) => state.cart.total);

  const handlePayment = async () => {
    try {
      const response = await fetch("/api/mercadopago/preference", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          items: cart.map((item) => ({
            title: item.name,
            quantity: item.quantity,
            price: item.price,
          })),
        }),
      });

      const preferenceData = await response.json();
      window.location.href = preferenceData.init_point; // Redirigir al flujo de pago
    } catch (error) {
      console.error("Error al generar el enlace de pago:", error);
    }
  };

  if (cart.length === 0) {
    return <p>Tu carrito está vacío.</p>;
  }

  return (
    <div>
      <h1>Resumen de Pago</h1>

      <h2>Carrito de Compras</h2>
      <ul>
        {cart.map((item) => (
          <li key={item.id}>
            <Image src={item.img} alt={item.name} width="50" height="50" />
            <p>{item.name}</p>
            <p>Fragancias: {item.fragance.join(", ")}</p>
            <p>Cantidad: {item.quantity}</p>
            <p>Precio Unitario: ${item.price.toFixed(2)}</p>
          </li>
        ))}
      </ul>

      <h3>Total: ${total.toFixed(2)}</h3>

      <button
        onClick={handlePayment}
        style={{
          padding: "10px 20px",
          backgroundColor: "#00b14f",
          color: "#fff",
          border: "none",
          borderRadius: "5px",
          cursor: "pointer",
        }}
      >
        Pagar con Mercado Pago
      </button>
    </div>
  );
}
